import { useEffect, useRef, useState } from 'react';

function CanvasDrawing() {
  const canvasRef = useRef(null);
  const contextRef = useRef(null);

  const [ctx, setCtx] = useState();
  const [isDrawing, setIsDrawing] = useState(false);
  // const [drawColor, setDrawColor] = useState("black");

  useEffect(() => {
    const canvas = canvasRef.current;
    canvas.width = window.innerWidth * 0.5;
    canvas.height = window.innerHeight * 0.5;

    const context = canvas.getContext("2d");
    context.strokeStyle = "black";
    context.lineWidth = 2.5;
    contextRef.current = context;

    setCtx(contextRef.current);
  }, [])

  // const drawingColor = () => {
  //   let colorArray = ["black", "white", "red", "blue", "purple"];
  //   let colorNum = Math.floor(Math.random() * colorArray.length);
  //   setDrawColor(colorArray[colorNum]);
  //   console.log(colorArray[colorNum]);
  // };
  const startDrawing = () => {
    setIsDrawing(true);
  };
  const finishDrawing = () => {
    setIsDrawing(false);
  };
  const drawing = ({ nativeEvent }) => {
    const { offsetX, offsetY } = nativeEvent;

    if (ctx) {
      if (!isDrawing) {
        ctx.beginPath();
        ctx.moveTo(offsetX, offsetY);
      } else {
        ctx.lineTo(offsetX, offsetY);
        ctx.stroke();
      }
    }
  };

  return (
    <div>
      <canvas
        style={{ border: "2px solid black" }}
        ref={canvasRef}
        onMouseDown={startDrawing}
        onMouseUp={finishDrawing}
        onMouseMove={drawing}
        onMouseLeave={finishDrawing}
      ></canvas>
      <br />
      <button onClick={() => { window.location.reload() }}>지우기</button>
      <br />
      {/* <button onClick={() => { drawingColor() }}>글씨 색 바꾸기</button> */}
    </div>
  )
}

export default CanvasDrawing;