import { Container } from 'react-bootstrap';
import { useEffect, useState } from 'react';

function Outro({tab}) {

  //화면전환 효과
  let [fade, setFade]= useState('');
  useEffect(() => {
    setFade('active');
    return () => {
      setFade('')
    }
  },[tab])

  return (
    <Container className={"contact pop " + fade}>
      <h4 className='enti1'>e-mail : eunji_ouo@naver.com</h4>
      <h4 className='enti2'>kakao openchat : <a href='https://open.kakao.com/o/sLFdBKff' rel='noreferrer' target='_blank'>click</a></h4>
    </Container>
  );
}

export default Outro;