import { useEffect, useState } from 'react';
import { Container } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import dataSlide from '../data/dataSlide';

function Intro() {
  let navigate = useNavigate();
  let [slide] = useState(dataSlide);

  //타이핑효과
  const [iam, setIam] = useState([]);
  useEffect(() => {
    const letters = ["안녕하세요! \n 신입 Frontend Developer \n 이은지입니다."];
    const speed = 100;
    let i = 0;

    function wait(ms) {
      return new Promise(res => setTimeout(res, ms))
    }

    const typing = async () => {
      const letter = letters[i].split("");
      let text = [];
      while (letter.length) {
        await wait(speed);
        text += letter.shift();
        setIam(text);
      }
      await wait(800);
    }
    setTimeout(typing, 1500);
    return () => {
      setIam([]);
    }
  }, [])
  
  return (
    <div className="intro">
      <Container>
        <div>
          <h1 className='my-5'>
            {iam}
          </h1>
          <p>
            저의 홈페이지에 방문해주셔서 감사합니다.{'\n'}
            상단 메뉴바를 통해 둘러보실 수 있습니다.
          </p>
        </div>
      </Container>
      <p style={{color:'orangered'}}>♥ WORKS ♥</p>
      <div className='autoslide'>
        <ul>
          {
            slide.map((v, i) => {
              return (
                <li key={slide[i].id} onClick={() => { navigate('/work/'+(i+1)) }}>
                  <img src={process.env.PUBLIC_URL + slide[i].img} alt={slide[i].title} />
                </li>
              )
            })
          }
        </ul>
        <ul className='clone'>
          {
            slide.map((v, i) => {
              return (
                <li key={slide[i].id} onClick={() => { navigate('/work/'+(i+1)) }}>
                  <img src={process.env.PUBLIC_URL + slide[i].img} alt={slide[i].title} />
                </li>
              )
            })
          }
        </ul>
      </div>
    </div>
  );
}

export default Intro;