import { Container } from 'react-bootstrap';
import Iframe from 'react-iframe';
import CanvasDrawing from '../component/canvasdrawing';

function Timeline() {
  return (
    <Container className='timeline'>
      <h3>[2023.04.21] 그림판 만들기!</h3>
      <CanvasDrawing />
      <h3>[2023.04.20] 나의 슬픈 스크래치 실력....</h3>
      <Iframe url="https://scratch.mit.edu/projects/838163995/embed"
                  width="100%"
                  height="800px"
                  allowtransparency="true"
                  frameborder="0"
                  scrolling="no"
                  allowfullscreen
                  display="block"
                  position="relative" />
    </Container>
  )
};

export default Timeline;