import { Container, Nav, Row, Col, Tab } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';
import Iframe from 'react-iframe';
import dataWork from '../data/dataWork';

function Work({tab}) {
  let [work] = useState(dataWork);
  let { id } = useParams();
  let selectWork = work.find((x) => x.id == id);

  //화면전환 효과
  let [fade, setFade]= useState('');
  useEffect(() => {
    setFade('active');
    return () => {
      setFade('')
    }
  },[tab])

  //미리보기 선택 고정
  let [key, setKey] = useState('content')
  useEffect(()=>{
    setKey('content')
  },[id])

  return (
    <Container className={"work mb-5 pop "+fade}>
      <h1 className='my-4'> : {selectWork.title}</h1>
      <Tab.Container id="left-tabs-example" defaultActiveKey="content" activeKey={key} onSelect={(e) => setKey(e)}>
        <Row>
          <Col sm={3}>
            <Nav variant="pills" className="flex-column">
              <Nav.Item>
                <Nav.Link eventKey="content">미리보기</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="preview">코드보기</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link href={selectWork.pageUrl} rel="noreferrer" target='_blank'>웹사이트로 이동</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link href={selectWork.docuUrl} rel="noreferrer" target='_blank'>작업물 문서화 보기</Nav.Link>
              </Nav.Item>
            </Nav>
          </Col>
          <Col sm={9}>
            <Tab.Content>
              <Tab.Pane eventKey="content">
                <h5>{selectWork.content}</h5>
                <h4>{selectWork.use}</h4>
                <Iframe url={selectWork.pageUrl}
                  width="100%"
                  height="600px"
                  display="block"
                  position="relative" />
              </Tab.Pane>
              <Tab.Pane eventKey="preview">
                <h5>{selectWork.codeExp}</h5>
                <img src={process.env.PUBLIC_URL+selectWork.codeUrl} alt='코드 구현'/>
              </Tab.Pane>
            </Tab.Content>
          </Col>
        </Row>
      </Tab.Container>
    </Container>
  );
}

export default Work;