import './App.scss';

import Intro from './routes/intro';
import Timeline from './routes/timeline';
import Profile from './routes/profile';
import Work from './routes/work';
import Contact from './routes/contact';

import { useEffect, useRef, useState } from 'react';
import { Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import { Container, Navbar, Nav, NavDropdown } from 'react-bootstrap';
import ReactAudioPlayer from 'react-audio-player';

function App() {
  let navigate = useNavigate();

  //로고 hover 효과
  let [Logo, setLogo] = useState('/img/logo_color.svg');
  const logo = useRef(null);
  useEffect(() => {
    const logoNode = logo.current;
    logoNode.addEventListener('mouseover', () => {
      setLogo('/img/logo_color_eye.svg');
    });
    logoNode.addEventListener('mouseout', () => {
      setLogo('/img/logo_color.svg');
    });
    return () => {
      logoNode.removeEventListener('mouseover', function () { });
      logoNode.removeEventListener('mouseout', function () { });
    }
  });

  //스크롤 상단 고정
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return (
    <div className="App">
      <Navbar sticky="top" expand="lg">
        <Container>
          <Navbar.Brand onClick={() => { navigate('/') }}><img ref={logo} src={process.env.PUBLIC_URL + Logo} alt='연두색 개구리모양 로고입니다.' /></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link onClick={() => { navigate('/timeline') }}>TimeLine</Nav.Link>
              <Nav.Link onClick={() => { navigate('/profile') }}>Profile</Nav.Link>
              <NavDropdown title="Works" id="basic-nav-dropdown">
                {/* <NavDropdown.Item onClick={() => { navigate('/work/0') }}>Vue</NavDropdown.Item> */}
                <NavDropdown.Item onClick={() => { navigate('/work/1') }}>SPAO</NavDropdown.Item>
                <NavDropdown.Item onClick={() => { navigate('/work/2') }}>RIDI</NavDropdown.Item>
                <NavDropdown.Item onClick={() => { navigate('/work/3') }}>AVATAR</NavDropdown.Item>
                <NavDropdown.Item onClick={() => { navigate('/work/4') }}>KEROPPI</NavDropdown.Item>
                <NavDropdown.Item onClick={() => { navigate('/work/5') }}>BFRO</NavDropdown.Item>
              </NavDropdown>
              <Nav.Link onClick={() => { navigate('/contact') }}>Contact</Nav.Link>
            </Nav>
            <ReactAudioPlayer
              src={process.env.PUBLIC_URL + "/bgm.mp3"}
              autoPlay
              loop
              controls
            />
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <div className='navigate'>
        <p className='flowerw' onClick={() => { navigate('/timeline') }}>TimeLine</p>
        <p className='flowerg' onClick={() => { navigate('/profile') }}>Profile</p>
        <p className='flowern' onClick={() => { navigate('/work/1') }}>Works</p>
        <p className='flowerp' onClick={() => { navigate('/contact') }}>Contact</p>
      </div>

      <Routes>
        <Route path="/" element={<Intro />} />
        <Route path="/timeline" element={<Timeline />} />
        <Route path="/profile" element={<Profile />} />
        <Route path="/work/:id" element={<Work />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="*" element={<div>없는 페이지 입니다.</div>} />
      </Routes>
    </div>
  );
}

export default App;
